@import 'colors';
@import 'buttons';

:local(.view) {
    ul {
        padding: 0;
    }

    li {
        display: inline-block;
        padding: .3em;
        width: 300px
    }

    p {
        margin: 0 auto;
    }

    td {
        border: .2em solid $dkblue;
        padding: 1.3em;
    }

    button {
        border-radius: 6px;
    }

}



